<template>
    <div class="row">

        <div class="col-2">
            <h6><i class="fas fa-project-diagram"></i> <fa-icon icon="object-ungroup" /> Nouns</h6>
            <div><a href="javascript:;" @click="showCreateNoun=!showCreateNoun"><fa-icon icon="plus-square" /></a></div>
            <div v-if="showCreateNoun" class="row no-gutters">
                <div class="col-10">
                    <input class="form-control form-control-sm" type="text" placeholder="Noun" v-model="noun" @input="err001=''" @keyup.enter="newNoun()" />
                    <span v-if="err001" class="badge text-danger">{{ err001 }}</span>
                </div>
                <div class="col-2">
                    <a href="javascript:;" class="btn btn-sm text-danger" @click="showCreateNoun=!showCreateNoun"><fa-icon icon="ban" /></a>
                </div>
            </div>
            <div class="row no-gutters" v-for="(noun, idx) in nouns" :key="idx">
                <div class="col">
                    <small :class="{del:usedVocabularies.indexOf(noun)!==-1, bold:usedVocabularies.indexOf(noun)===-1}">{{ noun }}</small>
                    <ul class="list-inline mb-0">
                        <li class="list-inline-item">
                            <a href="javascript:;" @click="addNode(noun, 'object', 'object')"><fa-icon icon="dice-d6" /></a>
                        </li>
                        <li class="list-inline-item">
                            <a href="javascript:;" @click="addNode(noun, 'object', 'actor')"><fa-icon icon="user" /></a>
                        </li>
                        <li class="list-inline-item">
                            <a href="javascript:;" class="text-danger" @click="removeNoun(idx)"><fa-icon icon="trash" /></a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>

        <div class="col-8">
            <div class="row">
                <ul class="list-inline mb-0 mt-1">
                    <li v-if="!isGenerated" class="list-inline-item">
                        <a href="javascript:;" @click="generateImage()"><fa-icon icon="image" /> Generate Image</a>
                    </li>
                    <li v-else class="list-inline-item">
                        <a href="javascript:;" @click="isGenerated=false"><fa-icon icon="undo" /></a>&nbsp;
                        <file-download :isPng="true" title="Download Diagram" :data="imageData" :downloadName="downloadFilename" />
                    </li>
                </ul>
            </div>
            <div class="row">
                <div id="static-diagram" ref="staticDiagram" class="col"></div>
            </div>
        </div>

        <div class="col-2 pt-5">
            <div v-if="tmpSelectionData.isLink===false">
                <div class="form-group row">
                    <label for="mark-as-done" class="col-8 col-form-label">Mark as done</label>
                    <div class="col-4">
                        <input type="checkbox" id="mark-as-done" class="form-control" :checked="tmpSelectionData.data.isDone" @change="onChangeIsDone($event.target.checked)"/>
                    </div>
                </div>
                <h6><b>Class Attributes:</b></h6>
                <div class="input-group input-group-sm mb-3">
                    <div class="input-group-prepend">
                        <div class="input-group-text">Name: </div>
                    </div>
                    <input type="text" class="form-control" :value="tmpSelectionData.data.key" @input="onChangeClassName($event.target.value)" />
                </div>
                <div v-if="tmpSelectionData.data.properties.length > 0" class="input-group input-group-sm">
                    <h6>Attributes:</h6>
                </div>
                <div class="input-group input-group-sm" v-for="(property, idx) in tmpSelectionData.data.properties" :key="idx">
                    <div class="input-group-prepend">
                        <div class="input-group-text pointer" @click="onChangeProperties(idx, 'access', property.access === 'public' ? 'static' : 'public')">{{ property.access }}</div>
                    </div>
                    <input type="text" class="form-control" :value="property.property" @input="onChangeProperties(idx, 'property', $event.target.value)" />
                    <input type="text" class="form-control" :value="property.type" @input="onChangeProperties(idx, 'type', $event.target.value)" />
                    <a href="javascript:;" class="text-danger fix" @click="removeProperty(idx)"><fa-icon icon="ban" /></a>
                </div>
            </div>
            <div v-if="tmpSelectionData.isLink">
                <h6><b>Relationships:</b></h6>
                <div class="input-group input-group-sm">
                    <div class="input-group-prepend">
                        <div class="input-group-text">From: </div>
                    </div>
                    <input type="text" class="form-control" disabled :value="tmpSelectionData.data.from" />
                </div>
                <erd-relation-chooser :value="tmpSelectionData.data.fromT" @change="onErdRelationChooserChange($event, 'fromT')" />
                <div class="input-group input-group-sm mt-2">
                    <div class="input-group-prepend">
                        <div class="input-group-text">To: </div>
                    </div>
                    <input type="text" class="form-control" disabled :value="tmpSelectionData.data.to" />
                </div>
                <erd-relation-chooser :value="tmpSelectionData.data.toT" @change="onErdRelationChooserChange($event, 'toT')" />
            </div>
        </div>
    </div>
</template>

<script>
    import slug from 'slug';
    import * as go from 'gojs';
    import { mapState } from 'vuex';
    import { StaticDiagramObjectNode, StaticDiagramLink, TmpStaticDiagramLink } from '../gotpls';
    import { VOCABULARY_SET_USED,VOCABULARY_NOUN_ADD,VOCABULARY_NOUN_REMOVE } from '../mutation-types';
    import FileDownload from '@/components/FileDownload';
    import ErdRelationChooser from '@/components/ErdRelationChooser';

    const $ = go.GraphObject.make;


    export default {
        name: "StaticDiagram",

        computed: {
            ...mapState(['project', 'nouns', 'used']),
            usedVocabularies() {
                return this.used.map(v => v.v);
            },
            downloadFilename() {
                return slug(this.project.name, {mode: 'rfc3986'}) + '_static.png';
            },
            newClass: function() {
                return function(key, category) {
                    return {key, category, isDone: false, properties: [], loc: ''};
                };
            },
            selectedItem() {
                return this.diagram.selection.first();
            }
        },

        data() {
            return {
                err001: null,
                showCreateNoun: false,
                noun: '',

                imageData: '',
                isGenerated: false,

                diagram: {},

                tmpSelectionData: {},
            }
        },

        mounted() {
            this.diagram = $(go.Diagram, this.$refs.staticDiagram, {
                layout: $(go.ForceDirectedLayout),
                'undoManager.isEnabled': true,
                'ChangedSelection': e => { this.onChangedSelection(e); },
            });

            let nodeTpls = new go.Map();
            nodeTpls.add('object', StaticDiagramObjectNode);
            this.diagram.nodeTemplateMap = nodeTpls;
            // let linkTpls = new go.Map();
            // this.diagram.linkTemplateMap = linkTpls;
            this.diagram.linkTemplate = StaticDiagramLink;

            this.diagram.contextMenu = $('ContextMenu',
                $('ContextMenuButton',
                    $(go.TextBlock, {margin: new go.Margin(6, 10, 6, 10), font: 'bold'}, 'New Class'), {click: e => {
                        e.diagram.commit(d => {
                            const data = this.newClass('New Class', 'object');
                            d.model.addNodeData(data);
                            const part = d.findPartForData(data);
                            part.location = d.toolManager.contextMenuTool.mouseDownPoint;
                        });
                    }}
                )
            );
            this.diagram.toolManager.linkingTool.temporaryLink = TmpStaticDiagramLink;
            this.diagram.toolManager.linkingTool.archetypeLinkData = {fromT: 'zero2many', toT: 'zero2many'};

            this.diagram.commandHandler.doKeyDown = () => {
                const e = this.diagram.lastInput;
                // add new property
                if (e.key.charCodeAt(0) === 13 && this.selectedItem instanceof go.Node) {
                    // insert property
                    this.diagram.model.commit(m => {
                        m.insertArrayItem(this.selectedItem.data.properties, -1, {access: 'public', property: 'property', type: 'string'});
                    }, 'new property');
                    return;
                }
                go.CommandHandler.prototype.doKeyDown.call(this.diagram.commandHandler);
            };
            this.diagram.model = $(go.GraphLinksModel, {
                nodeDataArray: [],
                linkDataArray: [],
            });
        },

        methods: {
            async newNoun() {
                let vocabulary = await this.$db.vocabularies.get({pid: this.project.id});
                if (vocabulary !== 'undefined') {
                    if (vocabulary.nouns.indexOf(this.noun) === -1) {
                        vocabulary.nouns.push(this.noun);
                        await this.$db.vocabularies.update(vocabulary.id, {nouns: vocabulary.nouns});
                    }
                } else {
                    vocabulary = {pid: this.project.id, nouns:[this.noun], verbs:[], used:[]};
                    await this.$db.vocabularies.add(vocabulary);
                }
                this.$store.commit(VOCABULARY_NOUN_ADD, this.noun);
            },

            async removeNoun(idx) {
                const noun = this.nouns[idx];
                let vocabulary = await this.$db.vocabularies.get({pid: this.project.id});
                let i;
                if ((i = vocabulary.nouns.indexOf(noun)) !== -1) {
                    vocabulary.nouns.splice(i, 1);
                    await this.$db.vocabularies.update(vocabulary.id, {nouns: vocabulary.nouns});
                }
                this.$store.commit(VOCABULARY_NOUN_REMOVE, idx);
            },

            generateImage() {
                this.imageData = this.diagram.makeImageData({scale: 1});
                this.isGenerated = true;
            },

            async addNode(noun, category, type) {
                this.diagram.model.addNodeData(this.newClass(noun, category));

                if (this.usedVocabularies.indexOf(noun) === -1) { // not found, add to `used`
                    let vocabulary = await this.$db.vocabularies.get({pid: this.project.id});
                    if (typeof vocabulary !== 'undefined') {
                        typeof vocabulary.used !== 'undefined' ?
                            vocabulary.used.push({v:noun,t:type}) :
                            (vocabulary.used = [{v:noun,t:type}])
                        ;
                        await this.$db.vocabularies.update(vocabulary.id, {used: vocabulary.used});
                    }
                    this.$store.commit(VOCABULARY_SET_USED, {v:noun,t:type});
                }
            },

            onChangeIsDone(v) {
                this.tmpSelectionData.data.isDone = v;
                if (this.selectedItem instanceof go.Node) {
                    this.diagram.model.commit(m => {
                        m.set(this.selectedItem.data, 'isDone', v);
                    }, 'toggle done');
                }
            },

            onChangeClassName(v) {
                this.tmpSelectionData.data.key = v;
                if (this.selectedItem instanceof go.Node) {
                    this.diagram.model.commit(m => {
                        m.set(this.selectedItem.data, 'key', v);
                    }, 'change class name');
                }
            },

            onChangeProperties(idx, key, v) {
                if (this.selectedItem instanceof go.Node) {
                    this.diagram.model.commit(m => {
                        m.set(this.selectedItem.data.properties[idx], key, v);
                    }, `change ${key} of attribute`);
                }
            },

            removeProperty(idx) {
                if (this.selectedItem instanceof go.Node) {
                    this.diagram.model.commit(m => {
                        m.removeArrayItem(this.selectedItem.data.properties, idx);
                    }, 'remove property');
                }
            },

            onChangedSelection(e) {
                if (this.selectedItem instanceof go.Node) {
                    return this.tmpSelectionData = {isLink: false, data: {...this.selectedItem.data}};
                } else if (this.selectedItem instanceof go.Link) {
                    return this.tmpSelectionData = {isLink: true, data: {...this.selectedItem.data}};
                }
                this.tmpSelectionData = {};
            },

            onErdRelationChooserChange(e, t) {
                if (this.selectedItem instanceof go.Link) {
                    // try to set the tmpSelectionData
                    this.tmpSelectionData.data[t] = e;
                    this.diagram.model.commit(m => {
                        m.set(this.selectedItem.data, t, e);
                    }, 'change ER arrow type');
                }
            },

        },

        components: {
            FileDownload, ErdRelationChooser
        }
    }
</script>

<style scoped>
    #static-diagram {
        background-color: var(--white);
        min-height: 30rem;
    }

    .del {
        text-decoration: line-through;
    }

    .bold {
        font-weight: bold;
    }

    .fix {
        position: relative;
        left: .3rem;
        top: .2rem;
    }
</style>
